import { Divider } from "@material-ui/core";
import React, { useContext, useState } from "react";
import {
  TextControl,
  TextControlPass,
} from "../../components/ui/atom/FormControls";
// import { NavbarLeft } from "../../components/ui/NavbarLeft";
import { AuthContext } from "../../context/AuthContext";
import FileUpload from "../../helpers/fileUpload";
import { useForm } from "../../hooks/useForm";
import defaultUser from "../../assets/defaultUser.png";

export const PerfilPage = () => {
  const { auth } = useContext(AuthContext);
  const { correo, images, name } = auth;
  const usuario = { correo, images, name };
  const [imgChange, setImgChange] = useState(false);
  const [passChange, setPassChange] = useState(false);

  const [formValues, onChange, reset, setFormValues] = useForm(usuario);

  const updateFilesCb = (e) => {
    setFormValues({
      ...formValues,
      images: e,
    });
  };

  return (
    <div className="flexbox-container-row">
      {/* <div className="col-md-2">
        <NavbarLeft />
      </div> */}{" "}
      <div className="col-md-2">
        <img
          src={formValues.images ? formValues.images : defaultUser}
          className="img-thumbnail animate__animated animate__fadeInDown mt-2 mb-2"
          alt={name}
        />

        <button
          className="btn btn-outline-warning mb-2"
          onClick={() => setPassChange(!passChange)}
        >
          Cambiar Contrasea.
        </button>
        <button
          className="btn btn-outline-warning"
          onClick={() => setImgChange(!imgChange)}
        >
          Cambiar foto perfil....
        </button>
        {imgChange && (
          <FileUpload
            updateFilesCb={updateFilesCb}
            accept=".jpg,.png,.jpeg,.bmp"
            label="Foto de Perfil"
          />
        )}
      </div>
      <div className="col-md-4 mt-5">
        <h6>Perfil de Usuario</h6>
        <Divider className="mt-2 mb-2" />
        <TextControl
          label={"Nombre*"}
          name={"name"}
          value={formValues.name}
          callBack={onChange}
        />
        <TextControl
          label={"Correo*"}
          name={"correo"}
          value={formValues.correo}
          callBack={onChange}
        />

        {passChange && (
          <>
            <Divider className="mt-2 mb-2" />

            <h6 className="mt-2 mb-2">Cambio de Contrasea</h6>

            <TextControlPass
              label={"Contrasea Actual*"}
              name={"name"}
              value={formValues.name}
              callBack={onChange}
            />
            <TextControlPass
              label={"Nueva Contrsea*"}
              name={"name"}
              value={formValues.name}
              callBack={onChange}
            />
            <TextControlPass
              label={"Repita Contrasea*"}
              name={"name"}
              value={formValues.name}
              callBack={onChange}
            />
          </>
        )}
      </div>
    </div>
  );
};
